import { Component, OnInit, HostListener, ViewChild } from '@angular/core';

import { MoveInOutService } from 'src/app/api/controllers/MoveInOut';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router } from '@angular/router';

@Component({
  selector: 'app-moveout-maintain',
  templateUrl: './moveout-maintain.component.html',
  styleUrls: ['./moveout-maintain.component.scss']
})
export class MoveoutMaintainComponent implements OnInit {

  isDataLoaded:boolean = false;

  moveOutData:string = "";
  moveOutDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  apartmentBlockId:any;
  apartmentBlockUnitId:any;

  blockUnitData:any;

  isMoveOutSubmitted:boolean = true;

  isError:boolean = false;
  alertMessage:string = "";

  selectedInput:string = "";
  columnField: any = {};
  listData: any;
  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private router: Router,
    private moveInOutService:MoveInOutService,
    private apartmentService:ApartmentService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }

  getDate(movein, date){

    this.moveOutDataList.map(obj => {
      if(obj.id == movein.id){
        obj.outDateData = moment(date).format("DD/MM/YYYY");
      }
   });

    return moment(date).format("DD/MM/YYYY"); 
  }

  getTime(time){
    var timeData = moment(time, 'HH:mm:ss').toDate();
    return moment(timeData).format('HH:mm A');
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }


  getBlockNo(item, id){
    var data = _.filter( this.blockUnitData, item => {
      if(item.apartmentBlockUnitId === id)
      return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.moveOutDataList.map(obj=> {
        if(obj.id == item.id){
          obj.apartmentBlockNumber = data[0].apartmentBlockNumber;
        }
      });

      return data[0].apartmentBlockNumber;
    }
  }

  getBlockUnitNo(item, id){
    var data = _.filter( this.blockUnitData, item => {
      if(item.apartmentBlockUnitId === id)
      return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.moveOutDataList.map(obj=> {
        if(obj.id == item.id){
          obj.apartmentBlockUnitNumber = data[0].apartmentBlockUnitNumber;
        }
      });

      return data[0].apartmentBlockUnitNumber;
    }
  }


  getApproveStatus(data){
    if(data == null)
      return 'high'
    else
      return 'low'
  }

  @HostListener('window:approveMoveIn', ['$event.detail'])
  approveMoveOut(detail){
    let data = this.datagrid.getrowdata(detail.rowId);
     var moveOutData = data;
     moveOutData.approvedBy =  parseInt(this.cookieService.get('userId'));
     moveOutData.approvedDate =  new Date().toISOString();

     this.isMoveOutSubmitted = false;

     this.moveInOutService.updateMoveOut(moveOutData).subscribe((res:any) => {

        if(res.message){
          this.isMoveOutSubmitted = true;
          this.sharedService.setAlertMessage("Moveout approved successfully");
        }
        else {
          this.isMoveOutSubmitted = true;
          this.isError = true;
          this.alertMessage = res.errorMessage;
        }

    },
    error => {
      this.isMoveOutSubmitted = true;
    });


   }

   onGlSearchFilter() {

    if(this.moveOutData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.moveOutData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }


  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Id',
         datafield: 'serialNo',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
      }, {
          text: 'Name',
          datafield: 'userName',
          width: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },{
         text: 'Tower Unit',
         datafield: 'block_Unit',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Phone',
         datafield: 'mobile',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Email',
         datafield: 'email',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Date',
         datafield: 'outDate',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD/MM/YYYY") +'</div>'
        },
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Time',
         datafield: 'outTime',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
     }, {
          text: 'Actions',
          cellsalign: 'center', 
          align: 'center',
          minwidth: 120,
          cellclassname: 'action-cell',
          cellsrenderer: (row, column, value): string => {
          let id_ = this.moveOutDataList[row].approvedBy;
          if(id_== null){
          return '<div class="simple-actions">'
           +'<a href="javascript:void(0)" role="button" data-toggle="dropdown" id="moveOutDropDown" aria-haspopup="true" aria-expanded="false">'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
         +'</a>'
         +'<div class="dropdown-menu action-menu dropdown-menu-right" aria-labelledby="moveOutDropDown">'
           +'<a href="javascript:void(0)" onClick="approveMoveOut('+ row +')">Approve</a>'
           +'<a href="javascript:void(0)" onClick="navigateTo('+ row +')">Edit</a>'
         +'</div>'
           +'</div>'
          }else if(id_==!null){
            return '<div class="simple-actions">'
            +'<a href="javascript:void(0)" role="button" data-toggle="dropdown" id="moveOutDropDown" aria-haspopup="true" aria-expanded="false">'
            +'<span class="action-dot"></span>'
            +'<span class="action-dot"></span>'
            +'<span class="action-dot"></span>'
          +'</a>'
          +'<div class="dropdown-menu action-menu dropdown-menu-right" aria-labelledby="moveOutDropDown">'
          +'<a href="javascript:void(0)" class="no-link grey">Approved</a>'
            +'<a href="javascript:void(0)" routerLink="/ams/moveinout-tracker/edit-moveout/{{moveout.id}}">Edit</a>'
          +'</div>'
            +'</div>'
          }
         
     },
      renderer: columnrenderer
  }];

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.moveInOutService.getMoveOutByApartmentId(params).subscribe((res:any) => {

      this.moveOutDataList = res.filter(item => {
        return item.isActive
      });

      console.log(this.moveOutDataList);
      this.gridSourceData = {
        localdata: this.moveOutDataList,
        datatype: "array"
      }
  
      this.listData = new jqx.dataAdapter(this.gridSourceData);

      this.totalItems = this.moveOutDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isDataLoaded = true;

    });

    this.apartmentService.getAllApartmentBlockUnits().subscribe((res:any) => {

      this.blockUnitData = res;

    });

  }

  @HostListener('window:navigateTo', ['$event.detail'])
  navigateTo(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let id = dataRecord.id
    this.router.navigateByUrl('/ams/moveinout-tracker/edit-moveout/' + id);

  }

}
function navigateTo(row){
  var event = new CustomEvent('navigateTo', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).navigateTo = navigateTo

function approveMoveOut(row){
  var event = new CustomEvent('approveMoveOut', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).approveMoveOut = approveMoveOut